/*  Needed for filters */
.hide { /* You can play with the seconds to change the "animation" */
  animation: hide 0.3s ease 0s 1 normal forwards;
  transform-origin: center;
}

.show { /* You can play with the seconds to change the "animation" */
  animation: show 0.3s ease 0s 1 normal forwards;
  transform-origin: center;
}

@keyframes hide {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
    width: 0;
    height: 0;
    margin: 0;
  }
}
@keyframes show {
  0% {
    transform: scale(0);
    width: 0;
    height: 0;
    margin: 0;
  }
  100% {
    transform: scale(1);
  }
}
