:root {
  --background: #F3F6F8;
  --foreground: #000;
  --divider: #FFFFFF;
  --overlay: #888;
}

html.overflow {
  overflow: hidden;
  position: relative;
  height: 100%;
}
html.overflow body {
  position: relative;
  height: 100%;
}

button {
  font-size: 1rem;
  border-radius: 1rem;
  border: 0.0625rem solid var(--divider);
  background: var(--background);
  color: var(--foreground);
  cursor: pointer;
}

#sheet {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  visibility: visible;
  transition: opacity 0.5s, visibility 0.5s;
  overflow: hidden;
}

#sheet[aria-hidden=true] {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

#sheet .overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: var(--overlay);
  opacity: 0.5;
}

#sheet .contents {
  border-radius: 1rem 1rem 0 0;
  background: var(--background);
  position: relative;
  --default-transitions: transform 0.5s, border-radius 0.5s;
  transition: var(--default-transitions);
  transform: translateY(0);
  max-height: 100vh;
  max-width: 70rem;
  margin: 0 auto;
  height: max-content;
  box-sizing: border-box;
  padding: 1rem;
  padding-top: 14px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
#sheet .contents .sheet {
  height: max-content;
}
#sheet .contents .sheet-header {
  text-align: center;
