.input-sizer {
  display: inline-grid;
  vertical-align: top;
  align-items: center;
  position: relative;
  border: solid 1px;
  padding: 0.25em 0.5em;
  margin: 5px;
}
.input-sizer.stacked {
  padding: 0.5em;
  align-items: stretch;
}
.input-sizer.stacked::after,
.input-sizer.stacked input,
.input-sizer.stacked textarea {
  grid-area: 2/1;
}
.input-sizer::after,
.input-sizer input,
.input-sizer textarea {
  width: auto;
  min-width: 1em;
  grid-area: 1/2;
  font: inherit;
  padding: 0.25em;
  margin: 0;
  resize: none;
  background: none;
  appearance: none;
  border: none;
}
.input-sizer span {
  padding: 0.25em;
}
.input-sizer::after {
  content: attr(data-value) " ";
  visibility: hidden;
  white-space: pre-wrap;
}
.input-sizer:focus-within {
  outline: solid 1px blue;
  box-shadow: 4px 4px 0px blue;
}
.input-sizer:focus-within > span {
  color: blue;
}
.input-sizer:focus-within textarea:focus,
.input-sizer:focus-within input:focus {
  outline: none;
}

.input-sizer {
  box-shadow: 4px 4px 0px #000;
}
.input-sizer > span {
  text-transform: uppercase;
  font-size: 0.8em;
  font-weight: bold;
  text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.15);
}

.toggle {
  cursor: pointer;
  display: inline-block;
}

.toggle__switch {
  display: inline-block;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 16px;
  width: 58px;
  height: 32px;
  position: relative;
  vertical-align: middle;
  transition: background 0.25s;
}
.toggle__switch:before, .toggle__switch:after {
  content: "";
}
.toggle__switch:before {
  display: block;
  background: #FFFFFF;
  box-shadow: 2px 0px 4px rgba(0, 0, 0, 0.16);
  border-radius: 50%;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 4px;
  left: 4px;
  transition: left 0.25s;
}
.toggle:hover .toggle__switch:before {
  background: #FFFFFF;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.5);
}
.toggle__checkbox:checked + .toggle__switch {
  background: #30A5E2;
}
.toggle__checkbox:checked + .toggle__switch:before {
  left: 30px;
}

.toggle__checkbox {
  position: absolute;
  visibility: hidden;
}

.toggle__label {
  margin-left: 5px;
  position: relative;
  top: 2px;
}

