@keyframes dash {
  0% {
    stroke-dashoffset: 1;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes animation-opacity-0-1 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes animation-text {
  0% {
    transform: translateY(-100px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes animate-plane-1 {
  0% {
    transform: translate(-600px, 90px);
    opacity: 1;
  }
  90% {
    transform: translate(380px, 90px);
    opacity: 1;
  }
  100% {
    transform: translate(380px, 90px);
    opacity: 0;
  }
}
@keyframes animate-plane-2 {
  0% {
    transform: translate(25px, 185px);
    opacity: 1;
  }
  90% {
    transform: translate(350px, -105px);
    opacity: 1;
  }
  100% {
    transform: translate(350px, -105px);
    opacity: 0;
  }
}
@keyframes animate-plane-3 {
  0% {
    transform: translate(100px, 150px);
    opacity: 1;
  }
  90% {
    transform: translate(375px, 45px);
    opacity: 1;
  }
  100% {
    transform: translate(375px, 45px);
    opacity: 0;
  }
}
@keyframes animate-plane-4 {
  0% {
    transform: translate(500px, -65px);
    opacity: 1;
  }
  90% {
    transform: translate(-165px, 10px);
    opacity: 1;
  }
  100% {
    transform: translate(-165px, 10px);
    opacity: 0;
  }
}
@keyframes animate-plane-5 {
  0% {
    transform: translate(500px, 200px);
    opacity: 1;
  }
  90% {
    transform: translate(-300px, -125px);
    opacity: 1;
  }
  100% {
    transform: translate(-300px, -125px);
    opacity: 0;
  }
}
